.hs-navigation {
    background-color: #7f95c7;
    color: #fff;
    width: 100%;
}
.hs-navigation-inner-wrapper {}

.hs-navigation-inner-wrapper ul {
    display: block;
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.hs-navigation-inner-wrapper > ul li {
    display: inline-block;
    vertical-align: middle;
}
.hs-navigation-inner-wrapper > ul li a {}

.hs-navigation-inner-wrapper > ul {}

.hs-navigation-inner-wrapper > ul > li {
    float: left;
}
.hs-navigation-inner-wrapper > ul > li > a {
    box-sizing: border-box;
    color: #002052;
    color: #fff;
    display: inline-block;
    font-family: Poppins, sans-serif;
    font-size: 16px;
    font-weight: 400;
    height: 100%;
    letter-spacing: .085em;
    line-height: 28px;
    padding: 0 23px;
    position: relative;
    text-transform: capitalize;
    vertical-align: middle;
    width: 100%;
}

@media(max-width:991px) {
     
    .hs-navigation-inner-wrapper > ul > li > a { 
        padding: 0 10px; 
    }
}

