        .container{
            position: relative;
        }
        .flex-row {
        background-color: #7f95c7;
        text-align:left;
        width: 100%;
        }

        .hs-profile-wrapper {
        background-color: #7f95c7;
        padding-left: 15px;
        padding-right: 15px;
        } 
        .hs-profile-pichure-wrapper {
        color: #fff;
        padding: 15px 0;
        } 
        .hs-profile-header {
        background-color: #7f95c7;
        color: #fff;
        padding-bottom: 15px;
        padding-top: 15px;
        } 
        .hs-author-bio-wrapper {
        background-color: #e8ebf4;
        color: #000;
        padding: 15px;
        } 
        .hs-education-wrapper {
        background-color: #e8ebf4;
        color: #000;
        padding: 0 15px 0;
        } 
        .hs-education-wrapper>div {
        padding: 15px 0;
        } 
        .hs-experience-wrapper {
        background-color: #7f95c7;
        color: #fff;
        padding: 0 15px 0;
        } 
        .hs-experience {
        padding-bottom: 15px;
        padding-top: 15px;
        } 
        .hs-live-project-wrapper {
        background-color: #7f95c7;
        color: #fff;
        padding: 0 15px 0;
        } 
        .hs-live-project{
        padding-bottom: 15px;
        padding-top: 15px;
        }

        h2.title {
        color: inherit;
        text-transform: capitalize;
        }
        .skils-tags span {
        border: 1px solid #000;
        border-radius: 2px;
        display: inline-block;
        font-size: 13px;
        line-height: 1.2;
        margin: 0 5px 5px 0;
        padding: 5px;
        text-transform: capitalize;
        vertical-align: middle;
        }
        .hs-languages p {
        font-size: 16px;
        font-weight: bold;
        text-transform: capitalize;
        }
        .hs-languages p span {
        display: inline-block;
        font-size: 13px;
        font-weight: 400;
        line-height: inherit;
        vertical-align: top;
        }
        .hs-live-project {}

        .hs-live-project ul {
        list-style-type: none;
        margin: 0;
        padding: 0;
        }

        .hs-live-project ul li {
        display: block;
        position: relative;
        }

        .hs-live-project ul li i {
        left: 0;
        position: absolute;
        top: 5px;
        vertical-align: middle;
        width: auto;
        } 
        .hs-live-project ul li a {
        color: #fff;
        display: inline-block;
        font-size: 18px;
        line-height: normal;
        padding-left: 20px;
        vertical-align: middle;
        word-break: break-word;
        }

        @media(max-width:991px) { 

        .h1, h1 {
        font-size: 2rem;
        }
        h2.title { 
        font-size: 1.5rem; 
        }
        }
        @media(max-width:767px) {
        h2.title {
        font-size: 1.5em;
        }
        }